@import url(https://fonts.googleapis.com/css2?family=Baloo+2&display=swap);
body {
  background-color: #0c4269;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-color: #0c4269;
  width: 100%;
  height: 100vh;
  font-family: 'Baloo 2', cursive;
}

.box {
  position: absolute;
  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  transform: translate(-50%, -50%);
  background-color: #252d4a;
  color: #fff;
  padding: 25px;
  border-radius: 10px;
  max-width: 600px !important;
}

h1 {
  text-align: center;
}


button.option {
  width: 100%;
  color: #ffffff;
  background-color: #252d4a;
  border-radius: 15px !important;
  padding: 5px;
  align-items: center;
  border: 5px solid #234668;
  cursor: pointer;
  margin-bottom: 10px;
}

button.back {
  width: 75px;
  color: #ffffff;
  background-color: #252d4a;
  border-radius: 15px !important;
  padding: 5px;
  margin-bottom: 25px;
  border: 5px solid #234668;
  cursor: pointer;
  text-align: center;
  float: left;
}

button:hover {
  background-color: #435185;
}

h4 {
  color: #ccc
}

h4 span {
  font-size: 60%;
}
